@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);



.country-list .spinner-container {
    width: 100%;
    min-height: 960px;
    position: relative;
}

.country-list .spinner-container .spinner-border {
    position: absolute;
    top: 20%;
    left: 50%;
}
.country-details{
    position: relative;
    min-height: 960px;
}

.country-details .country {
    position: absolute;
    top: 15%;
}

.country-details .country-stats ul{
    list-style-type: none;
    display: grid;
    grid-gap: 10px;
}

.country-details .border{
    padding: 5px;
    margin-right: 10px;
    border: 1px solid black;
}
*{
    font-family: 'Nunito', sans-serif;
    line-height: 1.5;
}
